import Vue from 'vue'
import Vuex from 'vuex'

import ringover from './ringover'

Vue.use(Vuex)

let RPC_ID = 1000000

import notes from './mocks/notes.json'

export default new Vuex.Store({
  state: {
    partnerId: null,
    partner: {},
    opportunities: [],
    notes
  },
  mutations: {
    setPartnerId (state, partnerId) {
      state.partnerId = partnerId
    },
    setPartner (state, partner) {
      state.partner = partner
    },
    setOpportunities (state, opportunities) {
      state.opportunities = opportunities
    }
  },
  actions: {
    async rpc (context, { endpoint, params }) {
      RPC_ID++
      const response = await fetch(endpoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          id: RPC_ID,
          jsonrpc: "2.0",
          method: "call",
          params
        })
      })
      const result = await response.json()
      return result
    },
    async getPartner({ dispatch, commit, state }, id) {
      id = id || state.partnerId
      const { action } = window.jumoBinding
      const data = await dispatch('rpc', {
        endpoint: '/web/dataset/call_kw/res.partner/read',
        params: {
          "args": [
            [id],
            [
              "same_vat_partner_id",
              "partner_gid",
              "additional_info",
              "meeting_count",
              "opportunity_count",
              "avatar_128",
              "image_1920",
              "__last_update",
              "is_company",
              "commercial_partner_id",
              "active",
              "country_code",
              "company_type",
              "name",
              "parent_id",
              "company_name",
              "type",
              "street",
              "street2",
              "city",
              "state_id",
              "zip",
              "country_id",
              "vat",
              "function",
              "phone_blacklisted",
              "mobile_blacklisted",
              "phone",
              "mobile",
              "phone_sanitized",
              "user_ids",
              "is_blacklisted",
              "email",
              "title",
              "active_lang_count",
              "lang",
              "comment",
              "message_follower_ids",
              "display_name"
            ]
          ],
          "model": "res.partner",
          "method": "read",
          "kwargs": {
            "context": {
              "bin_size": true,
              "lang": "en_US",
              "tz": action.context.tz,
              "uid": action.context.uid,
              "allowed_company_ids": action.context.allowed_company_ids,
              "params": {
                "id": id,
                "model": "res.partner",
                "view_type": "form"
              },
              "default_is_company": true
            }
          }
        }
      })
      // console.log(data)
      if (data.result && data.result.length) {
        commit('setPartner', data.result[0])
      } else {
        console.log('[JUMO] No partner found', data)
      }
    },
    async getOpportunities({ dispatch, commit, state }, partnerId) {
      partnerId = partnerId || state.partnerId
      const { action } = window.jumoBinding
      const data = await dispatch('rpc', {
          endpoint: '/web/dataset/search_read',
          params: {
            "model": "crm.lead",
            "domain": [
                ["partner_id.id", "=", partnerId]
            ],
            "fields": [
              "stage_id",
              "probability",
              "color",
              "priority",
              "expected_revenue",
              "kanban_state",
              "user_email",
              "user_id",
              "partner_id",
              "activity_summary",
              "active",
              "company_currency",
              "activity_state",
              "activity_ids",
              "name",
              "tag_ids",
              "campaign_id",
              "date_deadline"
            ],
            "limit": 3,
            "sort": "",
            "context": {
              "lang": "en_US",
              "tz": action.context.tz,
              "uid": action.context.uid,
              "allowed_company_ids": action.context.allowed_company_ids,
              "params": {
                "model": "res.partner",
                "view_type": "kanban"
              },
              "default_partner_id": partnerId,
              "active_model": "res.partner",
              "active_id": partnerId,
              "active_ids": [partnerId],
              "active_test": false,
              "bin_size": true
            }
          }
        })
      const result = data.result.records.map(item => {
        return {
          id: item.id,
          name: item.name,
          budget: item.expected_revenue,
          status: item.stage_id[1],
          deadline: item.date_deadline
        }
      })
      console.log(result)
      commit('setOpportunities', result)
    }
  },
  getters: {
    phoneNumber (state) {
      return state.partner.phone_sanitized ? state.partner.phone_sanitized.replace(/[^\d]/g, '') : ''
    }
  },
  modules: {
    ringover
  }
})
