<template>
  <transition name="fade">
    <div class="tree-menu">
      <div
        @click="toggleChildren"
        class="tree-menu__label"
        :class="labelClasses"
        :style="indent" 
      >
        <span v-if="nodes" class="tree-menu__arrow" :class="iconClasses">
          <svg width="21" height="13" viewBox="0 0 21 13" xmlns="http://www.w3.org/2000/svg">
            <path d="M19.875 1.19375C19.5237 0.844529 19.0485 0.648514 18.5531 0.648514C18.0578 0.648514 17.5825 0.844529 17.2312 1.19375L10.5 7.83125L3.86249 1.19375C3.51119 0.844529 3.03597 0.648514 2.54062 0.648514C2.04527 0.648514 1.57005 0.844529 1.21875 1.19375C1.043 1.36806 0.903515 1.57543 0.808324 1.80392C0.713132 2.0324 0.664124 2.27748 0.664124 2.525C0.664124 2.77252 0.713132 3.0176 0.808324 3.24608C0.903515 3.47457 1.043 3.68194 1.21875 3.85625L9.16874 11.8062C9.34305 11.982 9.55043 12.1215 9.77891 12.2167C10.0074 12.3119 10.2525 12.3609 10.5 12.3609C10.7475 12.3609 10.9926 12.3119 11.2211 12.2167C11.4496 12.1215 11.6569 11.982 11.8312 11.8062L19.875 3.85625C20.0507 3.68194 20.1902 3.47457 20.2854 3.24608C20.3806 3.0176 20.4296 2.77252 20.4296 2.525C20.4296 2.27748 20.3806 2.0324 20.2854 1.80392C20.1902 1.57543 20.0507 1.36806 19.875 1.19375Z"/>
          </svg> 
        </span> 
        <div v-if="Array.isArray(label)" class="tree-menu__children-text">
          <span v-for="(text, idx) in label" :key="idx">{{text}}</span>
        </div>
        <span v-else>{{ label }}</span>
      </div>
      <TreeMenu
        v-show="showChildren"
        v-for="node in nodes"
        :key="node.label.id"
        :nodes="node.nodes" 
        :label="node.label"
        :depth="depth + 1"
      >
      </TreeMenu>
    </div>
  </transition>
</template>

<script>
  export default { 
    props: [ 'label', 'nodes', 'depth' ],
    name: 'TreeMenu',
    data() {
      return { 
        showChildren: false 
      }
    },
    computed: {
      iconClasses() {
        return {
          'toggle': this.showChildren
        }
    },
    labelClasses() {
      return { 'has-children': this.nodes, 'has-no-children': !this.nodes }
    },
      indent() {
        return { transform: `translate(${this.depth * 1}rem)` }
      }
    },
    methods: {
      toggleChildren() {
        this.showChildren = !this.showChildren;
      }
    }
  }
</script>

<style lang="scss" scoped>
  .tree-menu {
    color: $black-text;
    font-size: clamp(1.2rem, 1.5vw, 2rem);
    
    &__label {
      display: flex;
      align-items: center;
      margin-bottom: 1.5rem;
      
      &.has-children {
        cursor: pointer;
        margin-bottom: 2rem;
      }

      &.has-no-children {
        padding-left: calc(clamp(1rem, 1vw, 1.2rem) + clamp(1rem, 1.2vw, 1.8rem));
        margin-bottom: 3rem;
      }
    }

    &__arrow {
      display: block;
      width: clamp(1.2rem, 1.5vw, 2.5rem);
      height: clamp(1.2rem, 1.5vw, 2.5rem);
      color: $primary-color;
      transform-origin: center;
      transition: all 0.3s;
      margin-right: clamp(1rem, 1.2vw, 1.8rem);

      svg {
        width: clamp(1.2rem, 1.5vw, 2.5rem);
        height: clamp(1.2rem, 1.5vw, 2.5rem);
        fill: $primary-color;
        transition: fill 0.3s;
      }
    
      &.toggle {
        transform: rotate(180deg);
        color: $main-green;
        transition: all 0.3s;

        svg {
          fill: $main-green;
        }
      }
    }

    &__children-text {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      span {
        &:not(:last-child) {
          margin-bottom: 1rem;
        }
      }
    }

    .tree-menu {
        &:last-child {
          margin-bottom: 4rem;
        }
      }
  }


  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */ {
    opacity: 0;
  }
</style>