<template>
  <section class="newsfeed">
    <h1 class="newsfeed__title">Historico de llamadas</h1>
    <div class="cards">
      <NewsFeedCard class="cards__item" v-if="selectedCall">
        <template #card-header>
          <span class="card__title">Última llamada</span>
          <div class="card-header__right">{{ selectedCall.start_time }}</div>
        </template>

        <template #card-main>
          <div class="card-content__contact">
            <div class="info-group">
              <span class="label">Email</span>
              <span class="text">{{ partner.email }}</span>
            </div>
          </div>

          <div class="card-content__table table">
            <div class="table__thead">
              <div class="table__th table__th_1">Al número</div>
              <div class="table__th table__th_2">Último estado</div>
              <div class="table__th table__th_3">Hora de comienzo</div>
              <div class="table__th table__th_4">Hora de finalización</div>
              <div class="table__th table__th_5">Duración total</div>
            </div>

            <div class="table__tbody">
              <div class="table__row">
                <div class="table__td table__td_1">{{ selectedCall.to_number }}</div>
                <div class="table__td table__td_2">{{ selectedCall.last_state }}</div>
                <div class="table__td table__td_3">{{ selectedCall.start_time }}</div>
                <div class="table__td table__td_4">{{ selectedCall.end_time }}</div>
                <div class="table__td table__td_5">{{ selectedCall.total_duration }}</div>
              </div>
            </div>
          </div>

          <div class="card-content__audio" v-if="selectedCall.record">
            <CustomAudioPlayer :record="selectedCall.record" :duration="selectedCall.incall_duration"/>
          </div>
        </template>
      </NewsFeedCard>

      <NewsFeedCard class="cards__item">
        <template #card-header>
          <span class="card__title">Historico de llamadas con cliente</span>
        </template>


        <template #card-main>
          <div class="card-content__table table">
            <div class="table__tbody" v-if="!missingConfig">
              <div 
                v-for="call in calls"
                :key="call.call_id + call.cdr_id"
                class="table__row"
                :class="{ 'table__row_selected': call === selectedCall }"
                @click="selectedCall = call"
              >
                <div class="table__td table__td_2">{{call.last_state}}</div>
                <div class="table__td table__td_3">{{call.start_time}}</div>
                <div class="table__td table__td_4">{{call.end_time}}</div>
                <div class="table__td table__td_5">{{call.total_duration}}</div>
              </div>
          
            </div>
            <div v-else class="">
              <h3>Please provide Ringover API token in the general settings page</h3>
            </div>
          </div>
        </template>
      </NewsFeedCard>
    </div>
  </section>
</template>

<script>
import NewsFeedCard from "@/components/NewsFeedCard";
import CustomAudioPlayer from '@/components/CustomAudioPlayer.vue'
import { mapState } from 'vuex'

export default {
  name: 'NewsFeed',
  components: { NewsFeedCard, CustomAudioPlayer },
  data () {
    return {
      selectedCall: null
    }
  },
  computed: {
    ...mapState({
      calls: state => state.ringover.calls,
      missingConfig: state => state.ringover.missingConfig,
      partner: state => state.partner
    }),
    lastCall () {
      if (this.calls.length) {
        return this.calls[0]
      }
      return null
    }
  },
  watch: {
    calls (newValue) {
      if (newValue.length) {
        this.selectedCall = newValue[0]
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .newsfeed {
    flex-grow: 1;
    transition: all 0.3s;
    height: 100%;
    margin-right: 0.8rem;
    margin-left: 0.8rem;
    background-color: #fff;
    border-right: 1px solid rgba(0, 0, 0, 0.38);

    &__title {
      color: $primary-color;
      font-weight: 600;
      font-size: 2.4rem;
      text-align: center;
      margin-top: 2rem;
      margin-bottom: 2.5rem;
    }

    .table__row:hover {
      background: #f7f8fa;
    }

    .table__row_selected {
      border: 1px solid #888;
      border-left: 0;
      border-right: 0;
    }

    .cards {
      height: 100%;
      overflow: scroll;
      &::-webkit-scrollbar { width: 0; }
      -ms-overflow-style: none;
      overflow: -moz-scrollbars-none; 


      &__item {
        margin-bottom: clamp(2rem, 2.5vw, 3.2rem);
      }
      
      /* .audio-group {    
        width: 100%;
        padding: 0 2rem;     

        audio {
          width: 100%;
          &::-webkit-media-controls-panel {
            background-color: #fff;
            border-radius: none;
          }

          &::-webkit-media-controls input[pseudo="-webkit-media-controls-timeline" i]::-webkit-slider-thumb {
            background-color: red;
            opacity: 1;
          }

          &::-webkit-media-controls-timeline {
            #thumb {
              opacity: 1;
              background: red !important;
            }
          }
          &::-webkit-media-controls-timeline::before {
            background-color: red;
          }

          &::-webkit-media-controls-play-button {

          }
        }
      } */
    }

    .card {
      &-content {
        &__audio {
          margin-top: 3rem;
          padding: 0 2rem;  
        }
      }
    }
  }
</style>
