<template>
  <aside class="sidebar">
    <TreeMenu 
      :label="treeData.label"
      :nodes="treeData.nodes"
      :depth="0"
    >
    </TreeMenu>
  </aside>
</template>

<script>
import { mapState } from 'vuex'
import TreeMenu from '@/components/TreeMenu.vue'

export default {
  name: 'Sidebar',
  components: { TreeMenu },
  data () {
    return {
    }
  },
  computed: {
    ...mapState(['opportunities']),
    treeData () {
      const tree = {
        label: 'Oportunidades',
        nodes: []
      }
      if (this.opportunities) {
        tree.nodes = this.opportunities.map(item => {
          return {
            id: item.id + '-top',
            label: item.name,
            nodes: [
              {
                id: item.id + '-sub',
                label: 'Presupuestos',
                nodes: [
                  {
                    id: item.id + '-info',
                    label: [
                      `Monto: ${item.budget}€`,
                      `Estado: ${item.status}`,
                      item.deadline ? `Cierre: ${item.deadline}`: undefined
                    ]
                  }
                ]
              }
            ]
          }
        })
      }
      return tree
    }
  }
}
</script>

<style lang="scss" scoped>
  .sidebar {
    padding: 2rem 1rem;
    width: 26%;
    background-color: #fff;
    -webkit-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.16);
    -moz-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.16);
    box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.16);
  }
</style>
