
export default {
    namespaced: true,
    state: {
        token: '',
        endpoint: '',
        calls: [],
        missingConfig: false
    },
    mutations: {
        setCalls (state, calls) {
            state.calls = calls
        },
        setConfig (state, { token, endpoint }) {
            state.endpoint = endpoint.replace(/\/$/, '')
            state.token = token
            if (!token) {
                state.missingConfig = true
            }
        }
    },
    actions: {
        async getConfig ({ commit, dispatch }) {
            const response = await dispatch('rpc', {
                endpoint: '/web/dataset/call_kw/res.config.settings/search_read',
                params: {
                    "args": [],
                    "model": "res.config.settings",
                    "method": "search_read",
                    "kwargs": {
                        "domain": [],
                        "fields": ["id", "jumo_ringover_token", "jumo_ringover_endpoint"],
                        "order": "id DESC"
                    }
                }
            }, { root: true })
            const config = response.result[0] || {}
            commit('setConfig', { token: config.jumo_ringover_token, endpoint: config.jumo_ringover_endpoint })
        },
        async getCalls ({ state, commit, dispatch }, { phoneNumber }) {
            if (!state.token && !state.missingConfig) {
                await dispatch('getConfig')
            }
            if (!state.token) {
                return
            }
            const response = await fetch(state.endpoint + '/v2/calls', {
                method: 'GET',
                headers: {
                    'Authorization': state.token
                },
                redirect: 'follow'
            })
            // TODO: filter via API
            const result = await response.json()
            commit('setCalls', result.call_list.filter(item => item.contact_number == phoneNumber))
        }
    }
}