<template>
  <div id="jumo-app">
    <article class="main-wrap">
      <ProfileSection/>
      <NewsFeed/>
      <Sidebar/>
    </article>
  </div>
</template>

<script>
import ProfileSection from '@/components/ProfileSection.vue'
import NewsFeed from '@/components/NewsFeed.vue'
import Sidebar from '@/components/Sidebar.vue'

export default {
  name: '',
  components: {
    ProfileSection,
    NewsFeed,
    Sidebar
  },
  data () {
    return {
    }
  },
  mounted () {
    this.$store.commit('setPartnerId', window.jumoBinding.action.context.active_id)
    this.$store.dispatch('getPartner').then(() => {
      this.$store.dispatch('ringover/getCalls', { phoneNumber: this.$store.getters.phoneNumber })
    })
    this.$store.dispatch('getOpportunities')
    document.documentElement.classList.add('jumo-page')
  },
  beforeDestroy () {
    document.documentElement.classList.remove('jumo-page')
  }
}
</script>

<style lang="scss" scoped>
  #app {
    height: 100%;
    background-color: rgb(83,83,83);
  }
  .main-wrap {
    position: relative;
    display: flex;
    width: 100%;
    height: calc(100vh - 46px);
    background-color: rgb(247, 248, 250);
    overflow: hidden;
  }
</style>
