<template>
  <div class="audio-group">
    <audio id="audio-player" ref="audio" preload="metadata">
      <source :src="record" type="audio/mp3" >
      Your browser does not support tag audio! (Su navegador no es compatible con la etiqueta de audio!)
    </audio>

    <div class="custom-audio">
      <button
        v-if="!isPlay"
        @click="playAudio"
        class="custom-audio__play-control custom-audio__play-control_play"
        id="play-icon">
      </button>
      <button
        v-else
        @click="pauseAudio"
        class="custom-audio__play-control custom-audio__play-control_pause"
        id="play-icon">
      </button>
      <span class="custom-audio__current-time time" id="current-time">{{calculateTime(currentTime)}}</span>
      <Slider
        v-model="rangeValue"
        class="custom-audio__track"
        :max="sliderMax"
        :tooltips="false"
        @update="rewindTrack"
      />
      <span class="custom-audio__duration time" id="duration">{{ formatedDuration}}</span>
    </div>

  </div>
</template>

<script>
import Slider from '@vueform/slider/dist/slider.vue2.js'

export default {
  name: 'CustomAudioPlayer',
  components: { Slider },
  data () {
    return {
      rangeValue: 0,
      isPlay: false,
      currentTime: 0,
      formatedDuration: 0,
      sliderMax: 100,
      intervalInstance: null
    }
  },
  props: {
    record: String,
    duration: Number
  },
  methods: {
    playAudio () {
      this.isPlay = true,
      this.$refs.audio.play()

      this.intervalInstance = setInterval(() => {

        this.currentTime = this.$refs.audio.currentTime;

        if (this.valueInSec < this.durationInSec ) {
          this.rangeValue += this.onePercentOfTrack 
        } else {
          this.pauseAudio()
          this.rangeValue = 0
          this.$refs.audio.currentTime = 0
        }
      } ,1000)

    },
    pauseAudio () {
      this.isPlay = false,
      this.$refs.audio.pause()
      clearInterval(this.intervalInstance)
    },
    calculateTime (secs) {
      const minutes = Math.floor(secs / 60);
      const seconds = Math.floor(secs % 60);
      const returnedSeconds = seconds < 10 ? `0${seconds}` : `${seconds}`;
      return `${minutes}:${returnedSeconds}`;
    },
    setDuration () {
      if (!this.$refs.audio) {
        return
      }
      this.formatedDuration = this.calculateTime(this.$refs.audio.duration);
    },
    rewindTrack () {
      this.$refs.audio.currentTime = this.valueInSec
      this.currentTime = this.$refs.audio.currentTime;
    }
  },
  computed: {
    onePercentOfTrack () {
      return this.durationInSec ? 100 / this.durationInSec : 1
    },
    durationInSec () {
      return this.duration //this.$refs.audio.duration
    }, 
    valueInSec () {
      return this.rangeValue / this.onePercentOfTrack
    }
  },
  mounted () {
    this.currentTime = this.$refs.audio.currentTime;
    if (this.$refs.audio.readyState > 0) {
      this.setDuration();
    } else {
      this.$refs.audio.addEventListener('loadedmetadata', () => {
        this.setDuration();
      });
    }    
  },
  watch: {
    record (newValue) {
      console.log(newValue)
      this.pauseAudio()
      this.$refs.audio.load()
    }
  }
}
</script>

<style lang="scss" scoped>
  .audio-group {
    #audio-player {
      appearance: none;
      -webkit-appearance: none;
      display: none;
    }

    .custom-audio {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &__play-control{
        @include resetDefaultButtonStyles;
        flex-grow: 0;
        width: 4rem;
        height: 4rem;
        min-width: 4rem;
        min-height: 4rem;
        border-radius: 50%;
        margin-right: 1rem;
        transition: background-color 0.2s;
      
        &:hover {
          background-color: darken(#fff, 8%)
        }

        &_play {
          @include svgBgimg(2rem, 2rem, '../assets/img/icons/play-button.svg');
          background-position: 12px center;
        }

        &_pause {
          @include svgBgimg(2rem, 2rem, '../assets/img/icons/pause.svg');
          background-position: center;
        }
    
      }

      &__current-time {
        flex-grow: 0;
        margin-right: 2rem;
      }

      &__duration {
        flex-grow: 0;
        margin-left: 2rem;
      }

      &__track {
        flex-grow: 1;
        --slider-bg: #805C79;
        --slider-connect-bg: rgb(75, 158, 155);
        --slider-tooltip-bg: rgb(75, 158, 155);
        --slider-handle-ring-color: #346e6c
      }
    }

  }



</style>
