<template>
  <section class="profile">
    <header class="profile-top">
      <div class="profile-info">
        <div class="profile-info__avatar">
          <img v-if="partner.id" :src="`/web/image?model=res.partner&id=${partner.id}&field=avatar_1920`" loading="lazy" alt="avatar">
        </div>
        <h2 class="profile-info__title">{{ partner.name }}</h2>
        <p class="profile-info__subtitle" v-if="partner.function">{{ partner.function }}</p>
      </div>

      <div class="profile-controls">
        <div class="profile-controls__item" @click="openNotes">
          <span class="image edit-note">
          </span>
          <span class="text">Notas</span>
        </div>
        <div class="profile-controls__item" @click="sendEmail">
          <span class="image add-email">
          </span>
          <span class="text">Email</span>
        </div>
        <div class="profile-controls__item">
          <span class="image call">
          </span>
          <span class="text">Llamar</span>
        </div>
        <div class="profile-controls__item" @click="scheduleEvent">
          <span class="image clock">
          </span>
          <span class="text">Actividad</span>
        </div>
      </div>
    </header>
    <main class="profile-content">
      <div class="profile-content__item profile-aditional">
        <div class="profile-aditional__header">
          <span class="icon" @click="toggleContent"
            :class="{toggle: toggleActive}"
          >
            <svg width="21" height="13" viewBox="0 0 21 13" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M19.875 1.19375C19.5237 0.844529 19.0485 0.648514 18.5531 0.648514C18.0578 0.648514 17.5825 0.844529 17.2312 1.19375L10.5 7.83125L3.86249 1.19375C3.51119 0.844529 3.03597 0.648514 2.54062 0.648514C2.04527 0.648514 1.57005 0.844529 1.21875 1.19375C1.043 1.36806 0.903515 1.57543 0.808324 1.80392C0.713132 2.0324 0.664124 2.27748 0.664124 2.525C0.664124 2.77252 0.713132 3.0176 0.808324 3.24608C0.903515 3.47457 1.043 3.68194 1.21875 3.85625L9.16874 11.8062C9.34305 11.982 9.55043 12.1215 9.77891 12.2167C10.0074 12.3119 10.2525 12.3609 10.5 12.3609C10.7475 12.3609 10.9926 12.3119 11.2211 12.2167C11.4496 12.1215 11.6569 11.982 11.8312 11.8062L19.875 3.85625C20.0507 3.68194 20.1902 3.47457 20.2854 3.24608C20.3806 3.0176 20.4296 2.77252 20.4296 2.525C20.4296 2.27748 20.3806 2.0324 20.2854 1.80392C20.1902 1.57543 20.0507 1.36806 19.875 1.19375Z" fill="#805C79"/>
            </svg>

          </span>
          <span class="text">Acerca de este contacto</span>
        </div>
        <div 
          class="profile-aditional__content"
          :class="{toggle: toggleActive}"
        >
          <div class="info-group" v-if="partner.email">
            <span class="label">Email</span>
            <span class="text">{{ partner.email }}</span>
          </div>
          <div class="info-group" v-if="partner.phone">
            <span class="label">Teléfono</span>
            <span class="text">{{ partner.phone }}</span>
          </div>
          <div class="info-group">
            <span class="label">Idioma</span>
            <span class="text">{{ partner.lang }}</span>
          </div>
          <div class="info-group" v-if="countryName">
            <span class="label">Canal de venta</span>
            <span class="text">{{ countryName }}</span>
          </div>
        </div>
      </div>
    </main>
    <Dialog v-if="notesOpen" @close="notesOpen = false" title="Notes">
      <template v-slot:content>
        <div class="notes-content"></div>
      </template>
    </Dialog>
  </section>
</template>

<script>
import Dialog from '@/components/Dialog.vue'
import { mapState } from 'vuex'

export default { 
  name: 'ProfileSection',
  components: { Dialog },
  data () {
    return {
      toggleActive: false,
      notesOpen: false
    }
  },
  methods: {
    toggleContent () {
      this.toggleActive = !this.toggleActive
    },
    openNotes () {
      this.notesOpen = true
      window.$('.o_ChatterTopbar_buttonLogNote').click()
    },
    scheduleEvent () {
      window.$('.o_ChatterTopbar_buttonScheduleActivity').click()
    },
    sendEmail () {
      this.notesOpen = true
      window.$('.o_ChatterTopbar_buttonSendMessage').click()
    }
  },
  computed: {
    ...mapState(['partner', 'notes']),
    countryName () {
      return this.partner.country_id ? this.partner.country_id[1] : null
    }
  },
  watch: {
    notesOpen (newValue) {
      if (newValue) {
        document.querySelector('#jumo-chatter').style.display = 'block'
      } else {
        document.querySelector('#jumo-chatter').style.display = 'none'
      }
    }
  },
  mounted () {
     document.querySelector('#jumo-chatter').style.display = 'none'
  }
}
</script>

<style lang="scss">
  #jumo-chatter {
    position: fixed;
    top: 70px;
    left: calc(50% - 485px);
    width: 970px;
    height: calc(100vh - 150px);
    z-index: 9999;
    overflow-y: scroll;
  }

  // .jumo-page .modal.o_technical_modal {
  //   z-index: 9999;
  // }

  // #jumo-app .modal.o_technical_modal {
  //   z-index: 999;
  // }
</style>

<style lang="scss" scoped>
  .notes-content {
    height: calc(100vh - 150px);
  }
  .profile {
    width: 26%;
    background-color: #fff;
    -webkit-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.16);
    -moz-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.16);
    box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.16);
    border-right: 1px solid rgba(0, 0, 0, 0.38);

    height: 100%;
    overflow: scroll;
    &::-webkit-scrollbar { width: 0; }
    -ms-overflow-style: none;
    overflow: -moz-scrollbars-none; 

    &-top {
      padding-top: 2.2rem;
      padding-bottom: 3.5rem;
      /* border-bottom: 1px solid rgba(0, 0, 0, 0.1); */
    }

    &-info {
      position: relative;
      display: flex;
      align-items: center;
      flex-direction: column;
      padding: 2rem clamp(1rem, 1.2vw, 2rem) clamp(1.5rem, 2.5vw, 3.5rem) clamp(1rem, 1.2vw, 2rem);
      margin-bottom: clamp(1.5rem, 2.5vw, 3.5rem);
      
      &:before {
        position: absolute;
        content: '';
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 80%;
        height: 1px;
        background-color: #000;
      }

      &__avatar {
        position: relative;
        width: clamp(10rem, 12vw, 15rem);
        height: clamp(10rem, 12vw, 15rem);
        min-width: clamp(10rem, 12vw, 15rem);
        min-height: clamp(10rem, 12vw, 15rem);
        border-radius: 50%;
        margin-bottom: clamp(1.5rem, 2.7vw, 2.5rem);
        -webkit-box-shadow: 1px 6px 4px 0px rgba(128, 92, 121, 0.9);
        -moz-box-shadow: 1px 6px 4px 0px rgba(128, 92, 121, 0.9);
        box-shadow: 1px 6px 4px 0px rgba(128, 92, 121, 0.9);

        img {
          position: relative;
          z-index: 10;
          border-radius: 50%;
          @include objectFitImg(clamp(10rem, 12vw, 15rem))
        }
      }

      &__title {
        font-weight: 500;
        font-size: clamp(1.8rem, 2vw, 4.8rem);
        color: $black-text;
        display: block;
        margin: 0 auto;
        text-align: center;
        line-height: 1;
        margin-bottom: 1.5rem;
      }

      &__subtitle {
        color: $secondary-text;
        font-weight: 500;
        font-size: clamp(1.4rem, 1.2vw, 2.5rem);
        line-height: clamp(2rem, 2vw, 3rem);
        margin: 0;
        text-align: center;
      }
    }

    &-controls {
      width: 80%;
      margin: 0 auto;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;

      &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        cursor: pointer;

        .image {
          width: clamp(3rem, 3vw, 4rem);
          height: clamp(3rem, 3vw, 4rem);
          min-width: clamp(3rem, 3vw, 4rem);
          background-position: center;
          background-size: contain;
          background-repeat: no-repeat;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-bottom: #{"max(1.2rem, 0.8vw)"};

          &.edit-note {
            background-image: url('../assets/img/icons/edit-task.png');
          }

          &.add-email {
            background-image: url('../assets/img/icons/new-message.png');
          }

          &.call {
            background-image: url('../assets/img/icons/telephone-call.png');
          }

          &.clock {
            background-image: url('../assets/img/icons/time.png');
          }
            /* 
          svg {
            @include svgStyle(clamp(1.5rem, 1.5vw, 2rem), $icon-blue)
          }

          &.call {
            svg {
              @include svgStyle(clamp(2rem, 2vw, 3rem), $icon-blue)
            }
          } */
        }

        .text {
          font-size: 1.4rem;
        }
      }

    }

    &-content {
      padding: 1.5rem clamp(1.5rem, 1.2vw, 2rem);

      &__item {
        margin-bottom: #{'max(2rem, 2.2vw)'};
      }

      .profile-aditional {
        &__header {
          padding: 1.4rem 0;
          display: flex;
          align-items: center;

          .icon {
            display: block;
            width: 2.5rem;
            height: 2.5rem;
            color: $primary-color;
            transform-origin: center;
            transition: all 0.3s;
            margin-right: clamp(1rem, 1.2vw, 1.8rem);

            &.toggle {
              transform: rotate(180deg);
              transition: all 0.3s;
            }
            
            svg {
              width: 2.5rem;
              height: 2.5rem;
              fill: $primary-color;;
            }
          }

          .text {
            color: $black-text;
            font-size: clamp(1.6rem, 2vw, 2.4rem);
            font-weight: 500;
          }
        }

        &__content {
          padding-top: clamp(1.5rem, 1.2vw, 2rem);
          padding-left: calc(1.8rem + clamp(1rem, 1.2vw, 1.8rem));
          opacity: 1;
          will-change: height, opacity, padding-top;
          transition: height 0.3s ease, opacity 0.3s ease, padding-top 0.3s ease;
          height: 100%;

          &.toggle {
            opacity: 0;
            height: 0;
            padding-top: 0;
            will-change: height, opacity, padding-top;
            transition: height 0.3s ease-in-out, opacity 0.3s ease-in-out, padding-top 0.3s ease-in-out;
            overflow-y: hidden;
          }

          .info-group {
            display: flex;
            align-items: flex-start;
            flex-direction: column;
            margin-bottom: clamp(1.8rem, 1.8vw, 2rem);

            .label {
              margin-bottom: 0.6rem;
              color: lighten($secondary-text, 25%);
              font-size: 1.6rem;
              font-weight: 600;
            }

            .text {
              color: $secondary-text;
              font-size: 1.8rem;
              font-weight: 600;
            }
          }

          
        }

      }
    }
  }
</style>
